import React, { FC } from 'react';
import s from './CheckBox.module.scss';

interface CheckBoxProps {
  checked: boolean;
  onClick: () => void;
}

const CheckBox: FC<CheckBoxProps> = ({ checked, onClick }) => {
  return (
    <label className={s.container}>
      <input
        type='checkbox'
        checked={checked}
        onClick={onClick}
      />
      <span className={s.checkmark}></span>
    </label>
  );
};

export { CheckBox };
